.header {
  position: relative;
  padding: 177px 0 197px 0;
  background: linear-gradient(113.69deg, #3503BA 11.81%, #06054B 86%, #06054B 86%);
}

.header__bg {
  position: absolute;
  top: 50%;
  right: 35px;
  transform: translateY(-50%);
}

.header__container {
  position: relative;
  margin-left: 10px;
  z-index: 1;
}

.header__title {
  margin-bottom: 5px;
  font-family: 'Montserrat', sans-serif;
  font-weight: bold;
  font-size: 45px;
  line-height: 55px;
  color: #FFF;
}

.header__paragraph {
  max-width: 542px;
  margin-bottom: 25px;
  font-family: 'Montserrat', sans-serif;
  font-size: 19px;
  line-height: 22px;
  color: #FFF;
}

@media (min-width: 1550px) {
  .header {
    padding: 233px 0 257px 0;
  }
  .header__bg {
    right: 100px;
  }
  .header__bg img {
    width: 830px;
  }
  .header__title {
    margin-bottom: 10px;
    font-size: 55px;
    line-height: 65px;
  }
  .header__paragraph {
    max-width: 603px;
    font-size: 24px;
    line-height: 27px;
  }
}