.software-stages {
  padding: 50px 0 65px;
  text-align: center;
  background-color: #020217;
}

.software-stages__title {
  margin-bottom: 60px;
}

.software-stages__container {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: left;
}

.software-stages__controls {
  margin-right: 95px;
}

.software-stages__controls-item {
  position: relative;
  min-width: 240px;
  margin-bottom: 10px;
  padding: 3px;
  font-family: "Raleway", sans-serif;
  font-weight: 500;
  font-size: 20px;
  line-height: 40px;
  color: #FFF;
  background: none;
  border-radius: 4px;
}

.software-stages__controls-item:last-child {
  margin-bottom: 0;
}

.software-stages__controls-item_active {
  background: linear-gradient(264.92deg, #AD36F0 -0.11%, #E9608C 99.89%);
}

.software-stages__controls-item span {
  display: flex;
  align-items: center;
  padding: 10px;
  background-color: #020217;
}

.software-stages__controls-item svg {
  margin-right: 10px;
}

.software-stages__stages-container {
  position: relative;
}

.software-stages__stages-page {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  opacity: 0;
  transition: 2s ease opacity;
  z-index: -1;
  list-style-position: inside;
}

.software-stages__stages-page li {
  font-family: "Raleway", sans-serif;
  font-size: 20px;
  line-height: 40px;
  font-feature-settings: 'pnum' on, 'lnum' on;
  color: #FFF;
}

.software-stages__stages-page_active {
  position: static;
  width: 460px;
  opacity: 1;
  transition: 2s ease opacity;
}