.btn {
  display: inline-block;
  position: relative;
  padding: 5px 18px;
  font-family: 'Raleway', sans-serif;
  font-weight: 500;
  font-size: 23px;
  line-height: 40px;
  color: #FFF;
  text-decoration: none;
  border: none;
  background: linear-gradient(266.76deg, #E29966 -0.11%, #E9608C 99.89%);
  border-radius: 10px;
  cursor: pointer;
}

.btn::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1;
  border-radius: 10px;
  background: linear-gradient(266.76deg, #E9608C 1.68%, #E29966 98.26%);
  opacity: 0;
  transition: 0.3s ease opacity;
}

.btn span {
  position: relative;
  z-index: 2;
  display: flex;
  align-items: center;
}

.btn:hover::after {
  opacity: 1;
  transition: 0.3s ease opacity;
}

.btn_scope {
  padding: 10px 12px;
  font-size: 16px;
  line-height: 19px;
  border-radius: 7px;
}

.btn svg {
  margin-left: 10px;
}

@media (min-width: 1550px) {
  .btn_scope {
    padding: 13px 15px;
    font-size: 18px;
    line-height: 21px;
  }
}