.software__header {
  position: relative;
  padding: 50px 0 45px;
  text-align: center;
  background: linear-gradient(95.5deg, #3503BA -4.05%, #06054B 86.86%);
}

.software__title {
  margin-bottom: 63px;
}

.software__header-container {
  margin-left: 181px;
  text-align: left;
}

.software__img {
  position: absolute;
  left: 112px;
  bottom: 45px;
}

.software__header-container {
  display: flex;
}

.software__header-item {
  margin-right: 40px;
}

.software__header-item:last-child {
  margin-right: 0;
}

.software__item-title {
  margin-bottom: 20px;
  font-family: 'Montserrat', sans-serif;
  font-weight: 600;
  font-size: 22px;
  line-height: 30px;
  color: #FFF;
}

.software__item-paragraph {
  padding: 10px 15px;
  max-width: 410px;
  font-family: "Raleway", sans-serif;
  font-weight: 500;
  font-size: 17px;
  line-height: 30px;
  color: #FFF;
  border: 2px solid #E9608C;
  border-radius: 6px;
}