.software-projects {
  padding: 50px 0 40px;
  background: linear-gradient(90deg, #3503BA 0%, #06054B 100%);
  text-align: center;
}

.software-projects__title {
  margin-bottom: 35px;
}

.software-projects__container {
  display: flex;
  align-items: center;
  text-align: left;
  flex-wrap: wrap;
}

.software-projects__item {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  padding: 28px 20px 20px;
  flex: 48%;
  height: 254px;
  margin-bottom: 30px;
  margin-right: 30px;
}

.software-projects__item:nth-child(3n+1) {
  flex: 100%;
  margin-right: 0;
}

.software-projects__item:nth-child(3n) {
  margin-right: 0;
}

.software-projects__item-hidden {
  position: relative;
  max-height: 0;
  overflow: hidden;
  transition: 0.5s ease max-height, 0.5s ease margin-top;
  z-index: 1;
}

.software-projects__item-visible {
  position: relative;
  font-family: 'Montserrat', sans-serif;
  font-weight: 600;
  font-size: 19px;
  line-height: 23px;
  font-feature-settings: 'pnum' on, 'lnum' on;
  color: #FFF;
  z-index: 1;
}

.software-projects__item-description {
  max-width: 393px;
  height: 80px;
  margin-bottom: 20px;
  font-family: "Raleway", sans-serif;
  font-weight: 500;
  font-size: 17px;
  line-height: 20px;
  font-feature-settings: 'pnum' on, 'lnum' on;
  color: #FFF;
}

.software-projects__item-description a {
  color: #FFF;
}

.software-projects__item:nth-child(3n+1) .software-projects__item-description {
  max-width: 569px;
}

.software-projects__item-tech {
  height: 55px;
  font-family: "Raleway", sans-serif;
  font-weight: 500;
  font-size: 14px;
  line-height: 16px;
  font-feature-settings: 'pnum' on, 'lnum' on;
  color: #FFF;
}

.software-projects__item:hover .software-projects__item-hidden {
  max-height: 160px;
  margin-top: 28px;
  transition: 0.5s ease max-height, 0.5s ease margin-top;
}

.software-projects__bg {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  object-fit: cover;
  z-index: 0;
}

.software-projects__bg img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}