.ml-scope {
  padding: 55px 0;
  background-color: #020217;
  text-align: left;
}

.ml-scope__container {
  display: flex;
  align-items: center;
  justify-content: center;
}

.ml-scope__controls {
  margin-right: 65px;
}

.ml-scope__controls-item {
  margin-bottom: 10px;
  padding: 3px;
  border-radius: 4px;
}

.ml-scope__controls-item_active {
  background: linear-gradient(264.92deg, #AD36F0 -0.11%, #E9608C 99.89%);
}

.ml-scope__controls-item:last-child {
  margin-bottom: 0;
}

.ml-scope__controls-item svg {
  margin-right: 10px;
}

.ml-scope__controls-item span {
  display: flex;
  align-items: center;
  padding: 10px;
  font-family: "Raleway", sans-serif;
  font-weight: 500;
  font-size: 20px;
  line-height: 30px;
  color: #FFF;
  background-color: #020217;
}

.ml-scope__info-page {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  opacity: 0;
  z-index: -1;
  font-family: "Raleway", sans-serif;
  font-size: 20px;
  line-height: 34px;
  font-feature-settings: 'pnum' on, 'lnum' on;
  color: #FFF;
  transition: 2s ease opacity;
}

.ml-scope__info-page_active {
  position: static;
  width: 630px;
  opacity: 1;
  transition: 2s ease opacity;
}

.ml-scope__info-title {
  margin-bottom: 35px;
}

.ml-scope__info-page ul {
  margin-bottom: 35px;
}

.ml-scope__info-page li {
  list-style-position: inside;
}

.ml-scope__info-container {
  position: relative;
}

.ml-scope__info-additional {
  margin-bottom: 35px;
}
