.navigation-bar {
  background-color: #020217;
  padding: 10px 0;
}

.navigation-bar__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.navigation-bar__link {
  position: relative;
  padding-left: 38px;
  margin-right: 20px;
  overflow: hidden;
  text-decoration: none;
}

.navigation-bar__link span {
  font-family: 'Montserrat', sans-serif;
  font-weight: 600;
  font-size: 18px;
  line-height: 22px;
  color: #fff;
  text-transform: uppercase;
}

.navigation-bar__link:last-child {
  margin-right: 0;
}

.navigation-bar__link span {
  position: relative;
  z-index: 2;
  cursor: pointer;
}

.navigation-bar__link-line {
  position: absolute;
  left: 0;
  width: 6px;
  height: calc(100% + 20px);
  background: linear-gradient(180deg, #E29966 0%, #E9608C 100%);
  z-index: 0;
  transform: translateX(10px) translateY(-14px) rotate(30deg);
}

.navigation-bar__link-line::after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: linear-gradient(180deg, #E9608C 0%, #E29966 65.4%);
  opacity: 0;
  transition: 0.3s ease opacity;
  transform: translate3d(0, 0, 0);
}

.navigation-bar__link:hover .navigation-bar__link-line::after {
  opacity: 1;
  transition: 0.3s ease opacity;
}

.navigation-bar__link-line_rigth {
  position: absolute;
  left: 11px;
  width: 6px;
  height: calc(100% + 20px);
  background: linear-gradient(180deg, #E29966 0%, #E9608C 100%);
  z-index: 0;
  transform: translateX(10px) translateY(-14px) rotate(30deg);
}

.navigation-bar__link-container {
  display: flex;
  align-items: center;
}

.navigation-bar__logo {
  transform: translate3d(0, 0, 0);
}

@media (min-width: 1400px) {
  .navigation-bar {
    padding: 15px 0;
  }
}

@media (min-width: 1600px) {
  .navigation-bar__link {
    margin-right: 30px;
  }
  .navigation-bar__link span {
    font-size: 22px;
    line-height: 30 px;
  }
}