.stack {
  padding: 30px 0 65px;
  text-align: center;
  background: linear-gradient(69.84deg, #3918BD -1.32%, #06054B 67.38%);
}

.stack__title {
  margin-bottom: 55px;
}

.stack__container {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: left;
}

.stack__controls {
  margin-right: 110px;
}

.stack__controls-item {
  position: relative;
  display: flex;
  align-items: center;
  padding: 10px;
  min-width: 282px;
  margin-bottom: 7px;
  font-family: "Raleway", sans-serif;
  font-weight: 500;
  font-size: 20px;
  line-height: 40px;
  color: #FFF;
  border: 3px solid transparent;
  border-radius: 4px;
}

.stack__controls-item_active {
  border-color: #E9608C;
}

.stack__controls-item::before {
  content: "";
  position: absolute;
  top: -3px;
  bottom: -3px;
  left: -3px;
  right: -3px;
  background: #000;
  border-radius: 4px;
  z-index: -1;
}

.stack__controls-item:last-child {
  margin-bottom: 0;
}

.stack__controls-item svg {
  margin-right: 10px;
}

.stack__tech-item {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
  font-family: "Raleway", sans-serif;
  font-size: 18px;
  line-height: 30px;
  color: #FFFFFF;
  font-feature-settings: 'pnum' on, 'lnum' on;
}

.stack__tech-item:last-child {
  margin-bottom: 0;
}

.stack__tech-box {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 44px;
  height: 44px;
  margin-right: 10px;
  background: linear-gradient(225deg, #AD36F0 0%, #3918BD 100%);
  border-radius: 10px;
}

.stack__tech-container {
  position: relative;
}

.stack__tech-page {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  opacity: 0;
  transition: 2s ease opacity;
  z-index: -1;
}

.stack__tech-page_active {
  position: static;
  width: 264px;
  opacity: 1;
  transition: 2s ease opacity;
}

@media (min-width: 1550px) {
  .stack {
    padding: 50px 0 85px;
  }
  .stack__controls-item {
    padding: 15px;
    min-width: 340px;
    margin-bottom: 10px;
    font-size: 23px;
    line-height: 45px;
  }
  .stack__tech-box {
    width: 55px;
    height: 55px;
    margin-right: 15px;
  }
  .stack__tech-box svg {
    width: 35px;
    height: 35px;
  }
  .stack__tech-item {
    margin-bottom: 13px;
    font-size: 20px;
    line-height: 32px;
  }
}