.contacts {
  padding: 25px 0 60px;
  text-align: center;
  background: linear-gradient(98.85deg, #3918BD -5.1%, #06054B 71.57%);
}

.contacts__title {
  margin-bottom: 73px;
}

.contatcs__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.contacts__company {
  padding: 10px 0;
  margin-bottom: 18px;
  width: 505px;
  font-family: 'Montserrat', sans-serif;
  font-weight: 600;
  font-size: 30px;
  line-height: 40px;
  color: #fff;
  background: linear-gradient(262.98deg, #E29966 0%, #E9608C 100%);
  border-radius: 6px;
}

.contacts__item-container {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.contacts__item {
  display: flex;
  align-items: center;
  margin-bottom: 25px;
  font-family: "Raleway", sans-serif;
  font-weight: 500;
  font-size: 17px;
  line-height: 30px;
  font-feature-settings: 'pnum' on, 'lnum' on;
  text-decoration: none;
  color: #fff;
}

.contacts__item:last-child {
  margin-bottom: 0;
}

.contacts__item svg {
  margin-right: 18px;
}

.contacts__form-title {
  margin-bottom: 27px;
  font-family: 'Montserrat', sans-serif;
  font-weight: 500;
  font-size: 22px;
  line-height: 25px;
  color: #fff;
}

.contacts__form {
  background-color: #fff;
  border-radius: 6px;
  padding: 15px;
  width: 500px;
  text-align: left;
}

.contacts__input-container {
  display: flex;
  align-items: center;
}

.contacts__input-container .contacts__input {
  flex: 49%;
  margin-right: 10px;
}

.contacts__input-container .contacts__input:last-child {
  margin-right: 0;
}

.contacts__input {
  padding: 0 10px;
  width: 100%;
  height: 32px;
  margin-bottom: 10px;
  font-family: "Raleway", sans-serif;
  font-weight: 500;
  font-size: 15px;
  line-height: 18px;
  color: rgba(2, 2, 23, 0.7);
  border: none;
  box-shadow: 0px 0px 4px rgba(57, 24, 189, 0.3);
  border-radius: 3px;
}

.contacts__input:focus {
  outline-color: rgba(2, 2, 23, 0.7);
}

.contacts__textarea {
  padding: 7px 10px;
  width: 100%;
  height: 134px;
  margin-bottom: 17px;
  resize: none;
  font-family: "Raleway", sans-serif;
  font-weight: 500;
  font-size: 15px;
  line-height: 18px;
  border: none;
  color: rgba(2, 2, 23, 0.7);
  box-shadow: 0px 0px 4px rgba(57, 24, 189, 0.3);
  border-radius: 3px;
}

.contacts__textarea:focus {
  outline-color: rgba(2, 2, 23, 0.7);
}

.contacts__form-description {
  margin-bottom: 10px;
  font-family: "Raleway", sans-serif;
  font-size: 9px;
  line-height: 11px;
  color: #020217;
}

.contacts__btn {
  position: relative;
  padding: 8px 17px;
  font-family: "Raleway", sans-serif;
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;
  color: #fff;
  background: linear-gradient(90deg, #E9608C 0%, #E29966 99.95%);
  border-radius: 3px;
  border: none;
  cursor: pointer;
}

.contacts__btn::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1;
  border-radius: 3px;
  background: linear-gradient(266.76deg, #E9608C 1.68%, #E29966 98.26%);
  opacity: 0;
  transition: 0.3s ease opacity;
}

.contacts__btn span {
  position: relative;
  z-index: 2;
  display: flex;
  align-items: center;
}

.contacts__btn:hover::after {
  opacity: 1;
  transition: 0.3s ease opacity;
}

.contacts__btn svg {
  margin-left: 10px;
}

@media (min-width: 1550px) {
  .contacts {
    padding: 45px 0 80px;
  }
  .contacts__form-title {
    margin-bottom: 35px;
    font-size: 25px;
    line-height: 28px;
  }
  .contacts__company {
    padding: 15px 0;
    margin-bottom: 23px;
    width: 545px;
    font-size: 32px;
  }
  .contacts__item {
    font-size: 20px;
    line-height: 33px;
  }
  .contacts__form {
    width: 700px;
  }
  .contacts__input {
    padding: 0 14px;
    height: 48px;
    margin-bottom: 14px;
    font-size: 18px;
    line-height: 20px;
  }
  .contacts__textarea {
    padding: 10px 14px;
    height: 150px;
    margin-bottom: 17px;
    font-size: 18px;
    line-height: 20px;
  }
  .contacts__form-description {
    margin-bottom: 15px;
    font-size: 12px;
    line-height: 13px;
  }
  .contacts__btn {
    padding: 10px 19px;
    font-size: 18px;
    line-height: 21px;
  }
}

@media (min-width: 1700px) {
  .contatcs__container {
    margin: 0 100px;
  }
}