.about {
  background-color: #020217;
  padding: 30px 0 65px;
  text-align: center;
}

.about__title {
  margin-bottom: 65px;
  text-align: center;
}

.about__container {
  display: flex;
  justify-content: space-between;
  text-align: center;
  margin: 0 20px;
}

.about__section-title {
  margin-bottom: 20px;
  font-family: 'Montserrat', sans-serif;
  font-weight: 600;
  font-size: 24px;
  line-height: 40px;
  color: #fff;
}

.about__container-bg {
  background: linear-gradient(251.56deg, #E9608C 0%, #3918BD 99.93%);
  border-radius: 6px;
  padding: 3px;
}

.about__section-container {
  padding: 25px 60px 25px 20px;
  display: flex;
  flex-direction: column;
  background-color: #020217;
}

.about__section-container_mass-media {
  padding: 22px 17px;
  border: none;
  background-color: #fff;
}

.about__competencies-item {
  margin-bottom: 25px;
  display: flex;
  align-items: center;
  font-family: "Raleway", sans-serif;
  font-weight: 500;
  font-size: 17px;
  line-height: 30px;
  color: #FFF;
}

.about__competencies-item:last-child {
  margin-bottom: 0;
}

.about__competencies-item svg {
  margin-right: 25px;
}

.about__mass-media-item {
  display: flex;
  align-items: center;
  margin-bottom: 19px;
}

.about__mass-media-item:last-child {
  margin-bottom: 0;
}

.about__mass-media-item div:first-child {
  width: 180px;
  overflow: hidden;
  flex-shrink: 0;
  margin-right: 5px;
  transform: translate3d(0, 0, 0);
  text-align: left;
}

.about__link {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  padding: 5px 10px;
  font-family: "Raleway", sans-serif;
  font-size: 17px;
  line-height: 30px;
  color: #020217;
  text-decoration: none;
  background-color: #fff;
  border-radius: 4px;
  transition: 0.4s ease background-color, 0.4s ease color;
}

.about__link svg {
  opacity: 0;
  margin-left: 25px;
  transition: 0.4s ease opacity;
}

.about__link * {
  z-index: 2;
}

.about__link::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background: linear-gradient(90deg, #E9608C 0.06%, #E29966 99.94%);
  transition: 0.4s ease opacity;
  opacity: 0;
  border-radius: 4px;
  z-index: 1;
}

.about__link:hover {
  color: #FFF;
  transition: 0.4s ease background-color, 0.4s ease color;
}

.about__link:hover::before {
  opacity: 1;
  transition: 0.4s ease opacity;
}

.about__link:hover svg {
  opacity: 1;
  transition: 0.4s ease opacity;
}

@media (min-width: 1550px) {
  .about {
    padding: 50px 0 85px;
  }
  .about__section-title {
    margin-bottom: 25px;
    font-size: 30px;
    line-height: 42px;
  }
  .about__section-container {
    padding: 35px 70px 35px 30px;
  }
  .about__competencies-item {
    margin-bottom: 30px;
    font-size: 20px;
    line-height: 33px;
  }
  .about__link {
    padding: 8px 13px;
    font-size: 17px;
    line-height: 30px;
  }
}

@media (min-width: 1700px) {
  .about__container {
    margin: 0 85px;
  }
}