/*base variable for project*/

.title {
  position: relative;
  display: inline-block;
  padding-left: 50px;
  font-family: 'Montserrat', sans-serif;
  font-size: 28px;
  line-height: 40px;
  font-weight: 600;
  color: #fff;
  text-transform: uppercase;
  overflow: hidden;
}

.title::after {
  content: "";
  position: absolute;
  left: 8px;
  width: 6px;
  height: calc(100% + 20px);
  background: linear-gradient(180deg, #E29966 0%, #E9608C 100%);
  z-index: 0;
  transform: translateX(10px) translateY(-14px) rotate(30deg);
}

.title::before {
  content: "";
  position: absolute;
  left: 20px;
  width: 6px;
  height: calc(100% + 20px);
  background: linear-gradient(180deg, #E29966 0%, #E9608C 100%);
  z-index: 0;
  transform: translateX(10px) translateY(-14px) rotate(30deg);
}

.title_policy {
  font-size: 19px;
  line-height: 40px;
}


/*base variable for project*/

@media (min-width: 1550px) {
  .title {
    font-size: 32px;
    line-height: 44px;
  }
}