.scope {
  padding: 30px 0 55px;
  background-color: #020217;
  text-align: center;
}

.scope__title {
  margin-bottom: 55px;
}

.scope__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  text-align: left;
}

.scope__item {
  width: max-content;
  padding: 22px 15px 17px 15px;
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0px 0px 5px #fff;
  transition: 0.3s ease box-shadow;
}

.scope__item:hover {
  transition: 0.3s ease box-shadow;
}

.scope__item-title {
  display: flex;
  align-items: center;
  margin-bottom: 25px;
  margin-left: 20px;
  margin-right: 20px;
}

.scope__item-title span {
  display: block;
  max-width: 164px;
  font-family: 'Montserrat', sans-serif;
  font-weight: 600;
  font-size: 18px;
  line-height: 22px;
  color: #020217;
}

.scope__item-title svg {
  flex-shrink: 0;
  margin-right: 20px;
}

.scope__item-container {
  display: flex;
  align-items: center;
}

.scope__item-paragraph {
  max-width: 329px;
  margin-bottom: 20px;
  font-family: 'Raleway', sans-serif;
  font-weight: 300;
  font-size: 16px;
  line-height: 19px;
  color: #020217;
}

.scope__item-paragraph span {
  word-wrap: none;
  word-spacing: normal;
}

.scope__item-link {
  display: flex;
  align-items: center;
  font-family: OpenSans, sans-serif;
  font-size: 17px;
  text-decoration: none;
  color: #000;
  transition: 0.3s ease color;
}

.scope__item-link:hover {
  color: #464040;
  transition: 0.3s ease color;
}

.scope__item-link:hover svg {
  left: 8px;
  transition: 0.3s ease left;
}

.scope__item-link svg {
  position: relative;
  left: 0;
  width: 25px;
  margin-left: 15px;
  transition: 0.3s ease left;
}

@media (min-width: 1550px) {
  .scope {
    padding: 50px 0 75px;
  }
  .scope__item {
    padding: 30px 23px 26px 23px;
  }

  .scope__item-title span {
    max-width: 204px;
    font-size: 21px;
    line-height: 24px;
  }
  .scope__item-paragraph {
    max-width: 346px;
    margin-bottom: 25px;
    font-size: 18px;
    line-height: 20px;
  }
}