.footer {
  padding: 11px;
  background-color: #020217;
}

.footer__container {
  display: flex;
  align-items: center;
  justify-content: center;
}

.footer__item {
  margin-right: 40px;
  font-family: 'Montserrat', sans-serif;
  font-weight: 500;
  font-size: 15px;
  line-height: 25px;
  color: #FFF;
  font-feature-settings: 'pnum' on, 'lnum' on;
  text-decoration: none;
}

.footer__item:last-child {
  margin-right: 0;
}