.ml__header {
  padding: 50px 0;
  background: linear-gradient(95.5deg, #3503BA -4.05%, #06054B 86.86%);
  text-align: center;
}

.ml__header-title {
  margin-bottom: 40px;
}

.ml__header-container {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: left;
}

.ml__header-img {
  margin-right: 75px;
}

.ml__header-paragraph {
  max-width: 553px;
  margin-bottom: 25px;
  font-family: "Raleway", sans-serif;
  font-weight: 500;
  font-size: 17px;
  line-height: 30px;
  color: #FFF;
}

.ml__formats {
  display: flex;
  align-items: center;
}

.ml__formats-item {
  display: flex;
  align-items: center;
  margin-right: 25px;
  font-family: "Raleway", sans-serif;
  font-weight: 600;
  font-size: 19px;
  line-height: 40px;
  color: #FFF;
}

.ml__formats-item:last-child {
  margin-right: 0;
}

.ml__formats-item svg {
  margin-right: 15px;
}

.ml__formats-title {
  margin-right: 25px;
  font-family: "Raleway", sans-serif;
  font-weight: 600;
  font-size: 17px;
  line-height: 40px;
  color: #FFF;
}

.ml__order {
  padding: 53px 0 35px;
  background: linear-gradient(73.96deg, #3503BA -51.85%, #06054B 130.17%);
}

.ml__order-title {
  margin-bottom: 28px;
  font-family: 'Montserrat', sans-serif;
  font-weight: 500;
  font-size: 22px;
  line-height: 25px;
  color: #FFF;
  text-align: center;
}

.ml__form {
  max-width: 500px;
  margin: 0 auto;
  padding: 15px;
  background-color: #fff;
  border-radius: 6px;
}

.ml__input {
  display: block;
  width: 100%;
  height: 32px;
  margin-bottom: 10px;
  padding: 0 10px;
  font-family: "Raleway", sans-serif;
  font-weight: 500;
  font-size: 15px;
  line-height: 18px;
  color: rgba(2, 2, 23, 0.7);
  box-shadow: 0px 0px 4px rgba(57, 24, 189, 0.3);
  border-radius: 3px;
  border: none;
}

.ml__input:focus {
  outline-color: rgba(2, 2, 23, 0.7);
}

.ml__input-container {
  display: flex;
  align-items: center;
}

.ml__input-container .ml__input:first-child {
  margin-right: 10px;
}

.ml__form-description {
  margin-bottom: 10px;
  font-family: "Raleway", sans-serif;
  font-size: 9px;
  line-height: 11px;
  color: #020217;
}

.ml__btn {
  position: relative;
  display: flex;
  align-items: center;
  padding: 8px 17px;
  font-family: "Raleway", sans-serif;
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;
  color: #FFF;
  background: linear-gradient(90deg, #E9608C 0%, #E29966 99.95%);
  border-radius: 3px;
  border: none;
  outline: none;
  cursor: pointer;
}


.ml__btn::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1;
  border-radius: 3px;
  background: linear-gradient(266.76deg, #E9608C 1.68%, #E29966 98.26%);
  opacity: 0;
  transition: 0.3s ease opacity;
}

.ml__btn span {
  position: relative;
  z-index: 2;
  display: flex;
  align-items: center;
}

.ml__btn:hover::after {
  opacity: 1;
  transition: 0.3s ease opacity;
}

.ml__btn svg {
  margin-left: 10px;
}

.ml__radio-input {
  display: none;
}

.ml__radio-input:checked+.ml__radio-btn {
  box-shadow: 0px 0px 4px #3918BD;
}

.ml__radio-btn {
  display: inline-flex;
  align-items: center;
  padding: 6px 10px;
  margin-right: 10px;
  font-family: "Raleway", sans-serif;
  font-size: 15px;
  line-height: 18px;
  color: rgba(2, 2, 23, 0.7);
  box-shadow: 0px 0px 4px rgba(57, 24, 189, 0.3);
  border-radius: 3px;
}

.ml__radio-btn:last-child {
  margin-right: 0;
}

.ml__radio-btn svg {
  margin-right: 10px;
}

.ml__radio-container {
  margin-bottom: 10px;
}