.policy {
  padding: 45px 0 10px;
  text-align: center;
  background-color: #020217;
}

.policy__title {
  margin-bottom: 55px;
}

.policy__container {
  text-align: left;
}

.policy__item {
  margin-bottom: 55px;
}

.policy__item:last-child {
  margin-bottom: 0;
}

.policy__paragraph-title {
  font-family: 'Montserrat', sans-serif;
  font-weight: 600;
  font-size: 18px;
  line-height: 40px;
  font-feature-settings: 'pnum' on, 'lnum' on;
  color: #FFF;
}

.policy__paragraph {
  font-family: "Raleway", sans-serif;
  font-size: 15px;
  line-height: 40px;
  font-feature-settings: 'pnum' on, 'lnum' on;
  color: #FFF;
}